.purpose__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  transition: all 0.3s ease-in-out;
  background: transparent;
  padding: 0 2.5rem;
  margin: 10rem auto 0 auto;
  max-width: 1905px;
  container-type: inline-size;
}

.purpose__container-p1 {
  line-height: 1.2;
  font-weight: 300;
  font-size: 1.3cqw;
  color: rgba(251, 224, 243, 0.7);
  letter-spacing: 0.25rem;
}

.purpose__container-h {
  font-size: 3cqw;
  font-weight: 400;
  line-height: 1;
  margin: 2.5rem 0;
  text-wrap: balance;
  text-wrap: nowrap;
}

.purpose__container-p2 {
  font-size: 1.04cqw;
  font-weight: 300;
  line-height: 1;
  color: rgba(251, 224, 243, 0.7);
  letter-spacing: 0.25rem;
}

.purpose__container-h span {
  color: #feffb3;
  font-size: 3cqw;
  font-weight: 400;
  line-height: 1;
}

@media (max-width: 768px) {
  .purpose__container-p1 {
    font-size: .75rem;
    font-weight: 300;
    line-height: .75rem;
  }

  .purpose__container {
    margin: 2.5rem 0 5rem 0;
    padding: 0 1rem;
  }
  
  .purpose__container-h, .purpose__container-h span {
    font-size: 4vw;
    line-height: 1.25;
  }

  .purpose__container-h {
    margin: 1rem 0;
  }
  
  .purpose__container-p2 {
    font-size: .75rem;
    line-height: .75rem;
    font-weight: 400;
    letter-spacing: 2px;
  }
}