.wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 10rem auto 0 auto;
}

.container {
    width: fit-content;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.container h2 {
    font-size: 3.5rem;
    font-weight: 400;
    line-height: 4rem;
    color: #FEFFB3;
    margin-bottom: .5rem;
    text-align: center;
}

.container h3 {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.5rem;
    color: #FBE0F3;
    letter-spacing: 4px;
    text-align: center;
    margin-bottom: 2.5rem;
}

.container p, .container span {
    font-size: 1.5rem;
    line-height: 1.5rem;
    font-weight: 400;
    color: #FBE0F3;
}

.container p {
    display: flex;
    width: fit-content;
}

.number {
    color: #FEFFB3;
    margin-right: 1.5rem;
}

.container div {
    border: 2px solid #FBE0F3;
    display: flex;
    flex-direction: column;
    gap: 2.5rem;
    padding: 2.5rem;
    margin-bottom: 2.5rem;
}   

.container a {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    font-size: 1.5rem;
    line-height: 1.5rem;
    letter-spacing: 4px;
    font-weight: 400;
    padding: 1rem 3rem;
    border: 2px solid #FBE0F3;
    border-radius: 50rem;
    color: #FBE0F3;
    transition: all .25s ease-in-out;
}

.container a:hover {
    border: 2px solid #E18186;
    background-color: #E18186;
    color: black;
}

@media (max-width: 768px) {
    .container h2 {
        font-size: 1.5rem;
        line-height: 1.5rem;
        text-align: center;
    }

    .container h3, .number, .container span {
        font-size: .75rem;
        line-height: .75rem;
    }

    .number {
        margin-right: .5rem;
    }

    .container h3 {
        letter-spacing: 2px;
    }

    .container div {
        padding: 1rem;
        gap: 1rem;
        display: inline-flex;
        max-width: 90vw;
        border: 1px solid #FBE0F3;
    }

    .container a {
        font-size: .75rem;
        line-height: .75rem;
        letter-spacing: 2px;
        padding: .5rem 1.25rem;
        gap: .5rem;
    }

    .container a svg {
        max-width: .5rem;
        max-height: .5rem;
    }

    .container a:hover {
        border: 1px solid #E18186;
    }
}