.diet__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  margin: 0 auto;
  max-width: 1905px;
  container-type: inline-size;
}

.diet__container-text {
  min-width: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.diet__container-content {
  display: grid;
  grid-template-columns: 1fr 3fr 1fr;
  align-items: center;
  width: 100%;
  margin-bottom: 1.5rem;
  justify-content: center;
}

.diet__container-plate {
  width: 100%;
  height: auto;
  object-fit: contain;
  align-self: center;
  filter: brightness(80%);
  z-index: -1;
}

.diet__container-content > img:first-of-type {
  justify-self: start;
}


.diet__container-content > img:last-of-type {
  justify-self: end;
}

.diet__container-text {
  justify-self: center;
  width: 100%;
  min-width: 0;
}

.diet__container-title {
  color: #feffb3;
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 4rem;
  margin-bottom: .5rem;
  scroll-margin-top: 1rem;
}

.diet__container-paragraph {
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.5rem;
  letter-spacing: 4px;
  color: rgba(251, 224, 243, 0.7);
  white-space: nowrap;
  margin-bottom: 2.5rem;
}

.diet__container-swiper :global(.swiper-button-prev) {
  background-image: url("/public/icons/left_btn.svg");
}

.diet__container-swiper :global(.swiper-button-next) {
  background-image: url("/public/icons/right_btn.svg");
}

.diet__container-detail {
  background: transparent;
  border: 2px solid #fbe0f3;
  border-radius: 100rem;
  padding: 1rem 3rem;
  width: fit-content;
  font-size: 1.5rem;
  font-weight: 400;
  line-height: 1.5rem;
  letter-spacing: 2.4px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  color: #FBE0F3;
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.diet__container-subcontent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.diet__container-detailModal {
  padding: 2.5rem 1.5rem 2.5rem 1.5rem;
  border: 2px solid #fbe0f3;
  border-radius: 3px;
  margin-top: 1rem;
  position: absolute;
  top: 100%;
  background-image: url("/public/images/pattern.webp");
  background-repeat: repeat;
  background-size: 600px 600px;
  image-rendering: pixelated;
  opacity: 0;
  transition: opacity 0.25s ease-in-out;
  pointer-events: none;
}

.isopen {
  opacity: 1;
}

.diet__container-list {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  align-items: start;
}

.diet__container-list li img {
  width: 1.5rem;
  height: 1.5rem;
}

.diet__container-list li {
  white-space: pre-wrap;
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.5rem;
  letter-spacing: 0.25rem;
  text-align: start;
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.diet__container-detail-active {
  background: #e18186;
  color: #222023;
  border: 2px solid #e18186;
}

:global(.swiper-slide) img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.diet__container-swiper {
  max-width: 40cqw;
}

.diet_slider_wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 2.5rem;
  gap: 1rem;
}

.diet_slider_wrapper button {
  background: transparent;
  padding: 0;
  margin: 0;
  height: 2.5rem;
  height: 2.5rem;
  cursor: pointer;
  border-radius: 50%;
  border: 2px solid #262427;
  box-sizing: border-box;
  outline: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.diet_slider_wrapper button svg {
  transition:
    filter 0.2s ease,
    opacity 0.2s ease;
  border-radius: 50%;
}

.diet_slider_wrapper button:disabled svg {
  filter: grayscale(1) brightness(0.7);
  opacity: 0.5;
  cursor: not-allowed;
}

@media (max-width: 768px) {

  .diet__container-swiper {
    max-width: 70vw;
  }

  .diet__container-title {
    font-size: 1.5rem;
    line-height: 1.5rem;
    letter-spacing: 0;
    margin-bottom: .5rem;
  }

  .diet__container-paragraph {
    font-size: .70rem;
    line-height: .75rem;
    letter-spacing: 2px;
    margin-bottom: 1.5rem;
    font-weight: 400;
  }

  .diet__container-paragraph-second {
    font-size: .75rem;
    line-height: .75rem;
    letter-spacing: 2px;
    margin-bottom: 1.5rem;
    white-space: pre-wrap;
    font-weight: 300;
    margin-bottom: 1.5rem;
  }

  .diet_slider_wrapper {
    max-width: 95vw;
    margin-bottom: 1rem;
  }

  .diet__container-plate {
    display: none;
  }

  .diet_slider_wrapper button, .diet_slider_wrapper button svg {
    width: 2rem;
    height: 2rem; 
  }

  :global(.swiper-slide) img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }

  .diet__container-content {
    display: flex;
    margin-bottom: 0;
  }

  .diet_slider_wrapper button {
    border: 1px solid black;
  }

  .diet__container-detail {
    font-size: .75rem;
    letter-spacing: 2px;
    line-height: .75rem;
    padding: .5rem 1rem;
    border: 1px solid #fbe0f3;
    gap: .5rem;
  }

  .diet__container-detail svg {
    width: .5rem;
    height: auto;
  }

  .diet__container-list li {
    font-size: .75rem;
    line-height: .75rem;
    gap: .5rem;
  }

  .diet__container-list {
    gap: .5rem;
  }

  .diet__container-detailModal {
    padding: .5rem .75rem;
  }

  .diet__container-list li svg {
    width: .75rem;
  }

  .diet__container-detail-active {
    border: 1px solid #e18186;
  }
}