.questions__container {
  padding: 0 3rem;
  max-width: 1905px;
  width: 100%;
  margin: 10rem auto 8rem auto;
}

.questions__container-title {
  margin-bottom: 2.5rem;
  font-size: 3.5rem;
  line-height: 1.25;
  font-weight: 400;
  text-align: center;
}

.questions__container-title span {
  color: #FEFFB3;
  font-size: 3.5rem;
  line-height: 1.25;
}

@media (max-width: 768px) {
  .questions__container-title, .questions__container-title span {
    font-size: 1rem;
    line-height: 1.25rem;
    margin-bottom: 1.5rem;
  }

  .questions__container {
    margin: 5rem auto 5.5rem auto;
    max-width: none;
    padding: 0;
    max-width: 90vw;
  }
}