.description {
  white-space: pre-wrap;
}

.title {
  padding: 1.5rem 0;
  border-bottom: 2px solid #fbe0f3;
  display: flex;
  cursor: pointer;
  line-height: 1;
  display: flex;
  align-items: center;
}

.title span {
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1;
  letter-spacing: 0.25rem;
  color: #fbe0f3;
  text-align: start;
}

.container:first-child {
  border-top: 2px solid #fbe0f3;
}

.title button {
  background: transparent;
  border: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
}

.icon {
  width: 2rem;
  height: 2rem;
  display: block;
  transition: transform 0.3s ease-in;
  margin-left: auto;
}

.description {
  color: #feffb3;
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1;
  letter-spacing: 0.25rem;
  transition: grid-template-rows .4s ease-in-out, padding .4s ease-in-out;
  display: grid;
  grid-template-rows: 0fr;
  padding: 0 2rem;
}

.descriptionText {
  overflow: hidden;
  transition: padding 0.4s ease-in-out;
  font-size: 1.5rem;
  letter-spacing: 4px;
}

.title.isopen .icon {
  transform: rotate(-45deg);
}

.title.isopen + .description {
  padding: 1rem 2rem;
  overflow: visible;
  grid-template-rows: 1fr;
}

@media (max-width: 768px) {
  .description {
    padding: 0 1rem;
  }
  .title {
    padding: .75rem 0;
    border-bottom: 1px solid #fbe0f3;
    align-items: start;
  }

  .title span {
    font-size: .75rem;
    line-height: .75rem;
    letter-spacing: 2px;
  }

  .icon {
    max-width: .5rem;
    height: auto;
  }

  .descriptionText {
    font-size: .5rem;
    line-height: .5rem;
    letter-spacing: 2px;
  }

  .title.isopen + .description {
    padding: .5rem 1rem;
  }
  
  .container:first-child {
    border-top: 1px solid #fbe0f3;
}
}