.getrid__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
  padding: 0 3rem;
  max-width: 1905px;
  width: 100%;
  margin: 0 auto;
  container-type: inline-size;
}

.getrid__container-text {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
}

.getrid__container-text h2 {
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 4rem;
  color: #fbe0f3;
  text-align: start;
}

.getrid__container-text p {
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 1.5rem;
  letter-spacing: 4px;
  color: rgba(254, 255, 179, 0.7);
  text-align: end;
}

.getrid__container-cards {
  display: flex;
  width: 100%;
  justify-content: space-between;
  margin-top: 2.5rem;
  transition: all 0.25s ease-in-out;
}

.getrid__container-cards > :nth-child(2),
.getrid__container-cards > :nth-child(4) {
  margin-top: 5rem;
}

@media (max-width: 768px) {
  .getrid__container-text {
    display: contents;
  }

  .getrid__container-text h2{
    font-size: 1rem;
    line-height: 1rem;
    font-weight: 400;
    grid-area: title;
    letter-spacing: 2px;
  }

  .getrid__container-text p {
    font-size: .75rem;
    line-height: .75rem;
    font-weight: 300;
    text-align: end;
    letter-spacing: 2px;
    grid-area: text;
    margin-top: 1.5rem;
  }
  
  .getrid__container {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: 
      "title"
      "cards"
      "text";
    padding: 2.5rem 1rem;
    max-width: none;
    margin: 0 auto;
  }

  .getrid__container-cards {
    display: grid;
    grid-template-columns: 1fr;
    grid-area: cards;
    column-gap: 1.5rem;
    margin-top: 1.5rem;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    gap: 1rem;
  }

  .getrid__container-cards > :nth-child(1),
  .getrid__container-cards > :nth-child(3) {
    margin: 0 25vw 0 0;
  }
  .getrid__container-cards > :nth-child(2),
  .getrid__container-cards > :nth-child(4) {
    margin: 0 0 0 25vw;
  }

}
