.body__container {
  display: flex;
  flex-direction: row;
  max-width: 1920px;
  margin: 0 auto;
  container-type: inline-size;
}

.body_container-img {
  height: min(calc(100svh - 70px), 900px);
  width: 100%;
  display: block;
  object-fit: cover;
  aspect-ratio: 2.5/2;
  object-position: top;
}

.body__container-content {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.5s ease-in-out;
  background: transparent;
}

.body__container-content {
  text-align: center;
}

.body__container-content h1 {
  font-size: 4.17cqw;
  font-weight: 400;
  line-height: 1.2;
  margin-bottom: 2.08cqw;
}

.body__container-content p {
  font-size: 1.25cqw;
  font-weight: 300;
  color: rgba(251, 224, 243, 0.7);
  line-height: 1;
  letter-spacing: 4px;
}

@media (max-width: 768px) {
  .body__container {
    flex-direction: column-reverse;
    align-items: center;
    margin-top: 2.75rem;
    max-width: none;
    width: 100%;
    margin-bottom: 5.5rem;
  }

  .body__container-content h1 {
    font-size: 2.5rem;
    line-height: 2.5rem;
    transform: translateY(-1.25rem);
    padding-bottom: .25rem;
  }

  .body__container-content p {
    font-size: .75rem;
    letter-spacing: 2px;
    font-weight: 300;
    line-height: .75rem;
    color: rgba(251, 224, 243, 0.7);
  }

  .body_container-img {
    max-width: 17.5rem;
    max-height: 20.5rem;
  }
}