.container__content {
  padding: 0 3rem 0 3rem;
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  max-width: 1905px;
  container-type: inline-size;
}

.text {
  font-size: 3cqw;
  font-weight: 400;
  text-align: center;
  grid-area: h2;
  text-wrap: nowrap;
  grid-area: h2;
}

.text span {
  color: #feffb3;
  font-size: 3cqw;
  line-height: 1;
}

.animationViewport {
  min-height: 100vh;
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
}

.card1 { grid-area: card1; }
.card2 { grid-area: card2; }
.card3 { grid-area: card3; margin-left:auto;}
.card4 { grid-area: card4; }
.card5 { grid-area: card5; transform: translate(-5.42cqw);}
.card6 { grid-area: card6; transform: translate(calc(100% + 1.5rem));}
.card7 { grid-area: card7; margin-left: auto;}

.group1, .group2, .group3 {
  display: flex;
  gap: 1.5rem;
  width: 100%;
}

.group2 {
  gap: 1.25cqw;
}

.group2 {
  justify-content: space-between;
  align-items: center;
}

@media (max-width: 768px) {
  .animationViewport {
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .container__content {
    width: 100%;
    margin: 0 auto;
    padding: 2.5rem 1rem;
    max-width: none;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;
    gap: 1rem;
    grid-template-areas:
      "card1"
      "card2"
      "card4"
      "h2"
      "card6"
      "card3"
      "card5"
      "card7";
    align-content: center;
  }

  .text, .text span {
    font-size: 1.5rem;
    line-height: 1;
    text-wrap: wrap;
  }

  .text {
    text-wrap: nowrap;
  }

  .group1, .group2, .group3 {
    display: contents;
  }
  .card5, .card6 { transform: none;}
  .card3, .card7 { margin-left: 0;}

  .card1 { transform: translateX(-25%); }
  .card2 { transform: translateX(25%); }
  .card4 { transform: translateX(-25%); }
  .card6 { transform: translateX(-25%); }
  .card5 { transform: translateX(25%); }
}


