.result-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  container-type: inline-size;
  max-width: 1905px;
  margin: 10rem auto;
}

.container__result-title {
  font-size: 3.5rem;
  font-weight: 400;
  line-height: 4rem;
  color: #fbe0f3;
  margin-bottom: 2.5rem;
}

.container__result-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.container__result {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.container__result-swiper {
  margin: 0 auto;
  overflow: hidden;
  max-width: 40cqw;
  width: 100%;
  position: relative;
}

.container__result-img {
  display: block;
  width: 100%;
  object-fit: cover;
  overflow: hidden;
  border-radius: 1px;
}

.container__result-swiper :global(.swiper-button-next svg),
.container__result-swiper :global(.swiper-button-prev svg) {
  display: none !important;
}

.container__result-swiper :global(.swiper-button-next),
.container__result-swiper :global(.swiper-button-prev) {
  background-repeat: no-repeat;
  background-position: center;
  width: 80px;
  height: 80px;
}

.container__result-swiper :global(.swiper-button-prev) {
  background-image: url("/public/icons/diet_left.svg");
}

.container__result-swiper :global(.swiper-button-next) {
  background-image: url("/public/icons/diet_right.svg");
}

.btn_left,
.btn_right {
  background: transparent;
  border: 0;
  padding: 0;
  outline: none;
  z-index: 2;
  cursor: pointer;
  border-radius: 50%;
  width: 80px;
  height: 80px;
}

.btn_left svg,
.btn_right svg {
  border-radius: 50%;
  transition:
    filter 0.2s ease,
    opacity 0.2s ease;
}

.container__result button:disabled svg {
  filter: grayscale(1) brightness(0.7);
  opacity: 0.5;
  cursor: not-allowed;
}

@media (max-width: 768px) {

  .container__result-swiper {
    margin: 0 auto;
    overflow: hidden;
    max-width: 70cqw;
    width: 100%;
    position: relative;
  }
  .container__result-title {
    font-size: 1.5rem;
    line-height: 1.5rem;
    margin-bottom: 2rem;
  }

  .container__result-swiper :global(.swiper-button-next),
  .container__result-swiper :global(.swiper-button-prev) {
    width: 2rem;
    height: 2rem;
  }

  .btn_left, .btn_right, .btn_left svg, .btn_right svg {
    max-width: 2rem;
    max-height: 2rem;
  }

  .btn_left svg{
    max-width: 2rem;
    max-height: 2rem;
  }

  .result-wrapper {
    margin: 5.5rem 0;
  }
}