.training__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  padding: 0 3rem 0 3rem;
  max-width: 1905px;
  width: 100%;
  margin: 10rem auto;
  container-type: inline-size;
}

.training__container-title {
  margin-bottom: 0.5rem;
  font-size: 2.92cqw;
  font-weight: 400;
  line-height: 1.2;
  transition: all 0.3s ease-in-out;
  background: transparent;
  scroll-margin-top: 1rem;
}

.training__container-paragraph {
  font-size: 1.25cqw;
  font-weight: 300;
  line-height: 1;
  color: rgba(254, 255, 179, 0.7);
  margin-bottom: 2.5rem;
  letter-spacing: 4px;
  transition: all 0.3s ease-in-out;
  background: transparent;
}

.training__container-content {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 1.67cqw;
  row-gap: 2.08cqw;
}

.training__text-and-price-block {
  grid-area: text;
  display: flex;
  flex-direction: column;
  align-items: start;
}

.training__container-item {
  display: grid;
  grid-template-columns: 1fr 2fr;
  width: fit-content;
  border: 2px solid #fbe0f3;
  padding: 2.08cqw;
  grid-template-areas: 
  "image title"
  "image text"
  "image btn";
  grid-template-rows: auto 1fr auto;
}

.training__container-item img {
  padding: 0 2.08cqw 0 0;
  width: 100%;
  max-width: 300px;
  aspect-ratio: 2/3;
  object-fit: cover;
  grid-area: image;
  display: block;
  object-position: center;
}

.training__container-sub-title {
  font-size: 2.08cqw;
  font-weight: 400;
  line-height: 1;
  color: #feffb3;
  margin: 0 0 1.25cqw 0;
  text-align: start;
  grid-area: title;
}

.training__container-sub-paragraph {
  font-size: 0.85cqw;
  font-weight: 200;
  line-height: 1;
  letter-spacing: 0.208cqw;
  color: rgba(251, 224, 243, 0.7);
  text-align: start;
  margin-bottom: 1.2cqw;
}

.training__container-buy-btn {
  border: 2px solid #fbe0f3;
  border-radius: 100rem;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fbe0f3;
  background: transparent;
  padding: 0.83cqw 2.92cqw;
  letter-spacing: 2.4px;
  transition: all 0.25s ease-in-out;
  white-space: nowrap;
  width: 100%;
  margin-top: auto;
  grid-area: btn;
  font-size: 1.25cqw;
  font-weight: 400;
  line-height: 1;
  cursor: pointer;
}

.training__container-buy-btn svg {
  width: 1.04cqw;
  height: 1.04cqw;
  padding: 0;
  margin-left: 1rem;
}

.training__container-subitem {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.training__container-limited {
  font-size: 1.5rem;
  line-height: 1;
  font-weight: 300;
  letter-spacing: 4px;
  color: rgba(254, 255, 179, 0.8);
  margin-top: 2.5rem;
}

.training__container-price {
  font-size: 1.25cqw;
  line-height: 1;
  font-weight: 400;
  color: #feffb3;
  margin-top: auto;
  margin-bottom: 1.67cqw;
}

.includes {
  cursor: not-allowed;
  background: linear-gradient(90deg,#F7F4FF, #CCBCFF);
  color: black;

}

@media (max-width: 768px) {
  .training__container-subitem, .training__container-text {
    display: contents;
  }

  .training__container-item img {
    align-self: start;
  }

  .training__container {
    max-width: none;
    margin: 5.5rem auto;
    padding: 0 1rem;
  }

  .training__container-title {
    font-size: 1.5rem;
    line-height: 1;
    margin: 0;
    margin-bottom: .5rem;
  }

  .training__container-paragraph {
    font-size: .75rem;
    line-height: 1;
    font-weight: 300;
    letter-spacing: 2px;
    max-width: 320px;
  }

  .training__container-content {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }

  .training__container-sub-title {
    font-size: 1.25rem;
    line-height: 1;
    letter-spacing: 1px;
    margin-bottom: 1.5rem;
  }

  .training__container-item img {
    min-height: 0;
    max-width: 100%;
    max-height: 100%;
    padding-right: .5rem;
    height: 100%;
    width: 100%;
  }

  .training__container-sub-paragraph {
    font-size: .75rem;
    font-weight: 400;
    line-height: 1;
    padding: 0;
    margin-bottom: 1rem;
    letter-spacing: 2px;
  }

  .training__container-item {
    display: grid;
    grid-template-columns: 120px 1fr;
    width: fit-content;
    border: 1px solid #fbe0f3;
    padding: .75rem 1rem;
    grid-template-areas: 
    "title title"
    "image text"
    "btn btn";
    grid-template-rows: auto 1fr auto;
  }

  .training__container-buy-btn {
    font-size: .75rem;
    line-height: 1;
    letter-spacing: 2px;
    padding: .5rem 2rem;
    border: 1px solid #fbe0f3;
    margin-top: 2rem;
  }

  .training__container-buy-btn svg {
    width: 100%;
    height: 100%;
    max-width: .5rem;
    max-height: .5rem;
    margin-left: .5rem;
  }

  .training__container-price {
    font-size: 1rem;
    font-weight: 300;
    line-height: 1;
    letter-spacing: 1px;
    white-space: nowrap;
    margin: auto 0 0 0;
  }

  .training__container-limited {
    font-size: .75rem;
    font-weight: 400;
    line-height: 1;
    letter-spacing: 2px;
    margin: 1.5rem   0 0 0;
  }
}