.container {
  width: 21cqw;
  height: 8cqw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: solid #fbe0f3 2px;
  border-radius: 3px;
  container-type: inline-size;
}

.number {
  position: absolute;
  top: 0;
  left: 0;
  padding: 0.75rem;
  font-size: 5cqw;
  font-weight: 400;
  line-height: 1;
  color: #fbe0f3;
  z-index: 10;
}

.container:hover,
.container:active {
  border: 2px solid #CCBCFF;
  color: #CCBCFF;
}

.container:hover p,
.container:active p {
  color: #CCBCFF;
}

.text,
.number,
.container {
  transition: color 0.25s ease-in-out, border 0.25s ease-in-out;
}

.container:hover .number,
.container:active .number {
  color: #CCBCFF;
}

.text {
  background: transparent;
  font-size: 5cqw;
  line-height: 1.25;
  letter-spacing: 1.5px;
  text-align: center;
  padding: 0 1.5rem;
}

@media (max-width: 768px) {
  .container {
    flex-shrink: 0;
    border: solid #FBE0F3 1px;
    max-width: 100%;
    max-height: 100%;
    text-wrap: nowrap;
    width: 52cqw;
    height: 18cqw;
  }

  .number, .text {
    font-size: .75rem;
    font-weight: 300;
    line-height: .75rem;
    letter-spacing: 1px;
    text-wrap: wrap;
  }

  .number {
    padding: 0.5rem;
  }

  .container:hover,
  .container:active {
    border: 1px solid #CCBCFF;
  }
}