.container {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 10rem;
}

.container h2 {
    font-size: 3.5rem;
    line-height: 4rem;
    font-weight: 400;
    color: #FEFFB3;
}

.container h3 {
    font-size: 1.5rem;
    line-height: 1.5rem;
    font-weight: 300;
    color: rgba(254, 255, 179, 0.7);
    letter-spacing: 4px;
    margin-top: .5rem;
}

.number, .paragraph {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.5rem;
    letter-spacing: 4px;
    color: #FEFFB3;
    margin: 2.5rem 0 1rem 0;
    text-align: center;
}

.paragraph {
    color: #FBE0F3;
    margin: 0;
}

@media (max-width: 768px) {

    .container {
        padding: 0 3rem;
        margin-bottom: 3.5rem;
    }

    .container h2 {
        font-size: 1.5rem;
        line-height: 1.5rem;
    }

    .container h3, .number, .paragraph {
        font-size: .75rem;
        line-height: .75rem;
        letter-spacing: 2px;
    }

    .container h3 {
        margin-bottom: .5rem;
    }

    .number {
        margin: 1.5rem 0 .5rem 0;
    }

}